

.product_page {
    position: fixed;
    z-index: 2;
    width: 100%;
    top: 0;
    bottom: 0;
}

.product_modal {
    position: relative;
    bottom: -6vh;
    width: 38vh;
    min-width: 70vw;
    min-width: 28rem;
    margin: 0 auto;
    background: white;
    z-index: 1;
    box-shadow: 0 3rem 3rem -3rem black;
    overflow-y: auto;
}

.product_modal::after {
    content: '';
    position: fixed;
    height: 15vh;
    background: linear-gradient(transparent, rgba(0,0,0,0.6));
    width: 38vh;
    min-width: 28rem;
    pointer-events: none;
    transform: translateY(-99.5%);
}

.outarea {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 44, 50, 0.9);
    z-index: 1;
}

.product_info {
    width: 100%;
}
.product_info span {
    font-size: 1.8rem;
    text-align: center;
    display: block;
    padding: 0 1rem;
    color: #0080aa;
    font-weight: 400;
    margin: 3rem 0;
}
    .product_info span:last-child {
        margin-bottom: 15vh;
    }
    .product_info span b {
        font-weight: 900;
    }
.product_flex header {
    width: 100%;
    padding: 2rem 1rem 2rem 1rem;
    background-color: #0080aa;
    color:white;
    box-sizing: border-box;
    text-align: center;
}

    .product_flex header h1 {
        font-size: 2.4rem;
        font-weight: 400;
    }

        .product_flex header span {
            font-size: 4vw;
            font-weight: 300;
            letter-spacing: 0.2vw;
            margin-bottom: 1rem;
            display: block;
        }

    .product_flex header img {
        display:none;
    }

.product_flex {
    display: flex;
    flex-direction: column;
    padding: 0.6rem;
    max-height: 80vh;
}

.product_price {
    background-image: url(../../assets/bg5.png);
    padding:2rem 0;
    text-align: center;
    color: #0080aa;
}

.product_price > article {
    width:90%;
    margin:0 auto;
    background:white;
    padding: 2rem 0;
    border-bottom: 0.6rem solid #0080aa;
}


.product_price form {
    position: sticky;
    top: 1rem;
}

.product_price article h2 {
    font-size: 6rem;
    font-weight: 400;
}
    .product_price h2 sup {
        font-size: 2rem;
        font-weight: 900;
    }
.product_price h3 {
    font-size: 2rem;
    font-weight: 400;
}
.product_price h5 {
    font-size: 1.6rem;
    font-weight: 400;
}

.product_price input {
    margin: 1rem auto;
    display: block;
}


.product_price > h2 {
    font-size: 2.6rem;
    font-weight: 400;
    margin: 1.6rem 0 0 0;
}

.product_info article {
    text-align: center;
    color: white;
    background: #0080aa;
    position: relative;
    margin-bottom: 0.6rem;
    padding: 1.6rem 0.6rem;
}

.product_info p {
    font-size: 2rem;
}

.click_out {
    position: relative;
    top: 2rem;
    color: white;
    text-align: center;
    font-size: 2rem;
    z-index: 1;
    display: block;
    pointer-events: none;
}

.youtube_cta {
    background: linear-gradient(red, #A00);
    width: 90%;
    display: block;
    padding: 1rem 2rem;
    margin: 2rem auto;
    box-sizing: border-box;
    font-size: 1.6rem;
    color: white;
    border-radius: 5rem;
}

.baixar_demonstracao {
    display:none;
}






















@media (min-width:90vh) {

    h2, h3, h5, p {
        cursor: default;
    }

    .product_flex header img {
        display: initial;
        position: absolute;
        height: 4rem;
        left: 2.4rem;
        top: 1.6rem;
        transform: rotate(-6deg);
    }
    .outarea {
        transition: background 0.3s ease;
    }
    .outarea:hover {
        background: rgba(0, 22, 25, 0.9);
    }

    .product_modal {
        min-width: 90%;
    }

    .click_out {
        font-size: 1rem;
        font-weight: 100;
        top: 1rem;
    }
    .product_flex {
        padding: 0;
        max-height: 90vh;
    }
    .product_flex > div {
        display: flex;
        flex-direction: row-reverse;
    }
    .product_flex header {
        width: 62%;
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 90%);
        height: 24vh;
        min-height: 24vh;
    }
    .product_flex header span {
        font-size: 0.6rem;
        margin-bottom: 0.4rem;
    }
    .product_flex header h1 {
        font-size: 1.6rem;
    }
    .product_info {
        width: 62%;
        display: grid;
        grid-template-columns: repeat(3, minmax(6rem, 1fr));
        grid-gap: 0.9rem;
        padding: 1rem;
        box-sizing: border-box;
        align-items: start;
    }
    .product_info p {
        font-size: 0.9rem;
    }
    .product_info span {
        font-size: 0.8rem;
        grid-column-start: 1;
        grid-column-end: 4;
        margin: 0.4rem 0;
    }
    .product_info article {
        background:linear-gradient(15deg, #0080aa, #20A0CA);
        margin-bottom:0;
    }
    .product_price {
        width: 38%;
        margin-top: -24vh;
        padding: 4rem 0 0 0;
    }
    .product_price > article {
        position: sticky;
        top: 3rem;
        width: 53%;
        min-width: 12rem;
        border-bottom: 0.4rem solid #0080aa;
        box-shadow: 0 1rem 2rem -1rem;
        border-radius: 1rem;
        transition:transform 0.3s ease-out, box-shadow 0.3s ease-out;
        z-index: 2;
        margin-top: 3rem;
    }

    .product_price > article:hover {
        transform:translateY(-1rem);
        box-shadow: 0 2rem 3rem -1rem;
    }
    .product_price > h2 {
        display: none;
    }
    .product_price h3, .product_price h5 {
        font-size: 0.9rem;
        line-height: 1.4rem;
    }
    .product_price article h2 {
        font-size: 3.8rem;
    }
    .product_modal::after {
        width: 54.8%;
        background: linear-gradient(transparent, rgba(0,0,0,0.3));
    }
    .product_price h2 sup {
        font-size: 1.4rem;
    }

    .right_block {
        grid-column-start: 2;
        grid-column-end: 4;
    }
    .left_block {
        grid-column-start: 1;
        grid-column-end: 3;
    }
    .product_price form {
        position: initial;
        top: unset;
    }
    .product_price input {
        height: 2rem;
        max-width: 80%;
        margin: 0.6rem auto;
    }
    .youtube_cta {
        width: 62%;
        min-width: 13rem;
        font-size: 0.9rem;
        padding: 0.4rem 0.6rem;
        position: -webkit-sticky;
        position: sticky;
        top: 22rem;
        margin: 38% auto 0 auto;
        border: 0.2rem solid orangered;
        background: none;
        color: orangered;
        box-shadow: 0 0.6rem 1rem -0.4rem;
    }

    .youtube_cta:hover {
        background:white;
    }

    .baixar_demonstracao {
        display: initial;
        background: #0080aa;
        color: white;
        border-radius: 0.2rem;
        padding: 0.3rem 1rem;
    }

        .baixar_demonstracao:hover {
            background: #0090bb;
        }
}